<template>
    <div class="main">
        <div class="paymentSetting">
            <div class="searchA">
                <el-select v-model="search.fee_type" clearable placeholder="请选择" size="small">
                    <el-option v-for="item in fee_type_list" :label="item.dictionary_name" :value="item.dictionary_value" :key="item.dictionary_value"></el-option>
                </el-select>
                <el-button size="small" type="primary" plain icon="el-icon-search" @click="serchList()">查询</el-button>
            </div>
            <div class="home_table">
                <el-table :data="tableData" border v-loading="isLoading" element-loading-background="rgba(0, 0, 0, 0.4)" size="medium" :max-height="tableHeight" style="width: 100%">
                    <template v-for="(item, index) in tableHead">
                        <el-table-column :prop="item.prop" :label="item.title" :key="index" :show-overflow-tooltip="true" :min-width="item.minWidth"></el-table-column>
                    </template>
                    <el-table-column prop="address" align="center" label="操作" fixed="right" width="120">
                        <template slot-scope="scope">
                            <el-button type="text" size="small" @click="check(scope.row)">查看</el-button>
                        </template>
                    </el-table-column>
                </el-table>
                <div class="pagination">
                    <el-pagination background :current-page.sync="pageNum" :page-sizes="[15, 30, 50, 100]" :page-size="pageSize" layout="total, sizes, prev, pager, next" :total="total" class="pagination" @size-change="handleSizeChange" @current-change="handleCurrentChange" :hide-on-single-page="total<15? true:false" />
                </div>
            </div>
            <!-- 新增编辑弹窗 -->
            <el-dialog title="查看" v-dialogDrag :visible.sync="dialogVisibleCheck" width="60%" :close-on-click-modal="false">
                <el-descriptions direction="vertical" :column="8" border>
                    <el-descriptions-item label="缴费类型">{{formInfo.fee_type_name}}</el-descriptions-item>
                    <el-descriptions-item label="应缴金额">{{formInfo.payable_fee}}</el-descriptions-item>
                    <el-descriptions-item label="缴费班级">{{formInfo.class_name}}</el-descriptions-item>
                    <el-descriptions-item label="预计缴费人数">{{formInfo.predict_count}}</el-descriptions-item>
                    <el-descriptions-item label="预计费用">{{formInfo.predict_fee}}</el-descriptions-item>
                    <el-descriptions-item label="已交人数">{{formInfo.pay_count}}</el-descriptions-item>
                    <el-descriptions-item label="已交费用">{{formInfo.pay_fee}}</el-descriptions-item>
                    <el-descriptions-item label="未交费用">{{formInfo.unpay_fee}}</el-descriptions-item>
                </el-descriptions>
                <div class="home_table" style="margin-top:10px">
                    <el-table :data="tableDataInfo" border element-loading-background="rgba(0, 0, 0, 0.4)" size="medium" style="width: 100%">
                        <template v-for="(item, index) in tableHeads">
                            <el-table-column :prop="item.prop" :label="item.title" :key="index" :show-overflow-tooltip="true" :min-width="item.minWidth"></el-table-column>
                        </template>
                    </el-table>
                    <div class="pagination">
                        <el-pagination background :current-page.sync="pageNumInfo" :page-sizes="[10, 20, 30, 50]" :page-size="pageSizeInfo" layout="total, sizes, prev, pager, next" :total="totalInfo" class="pagination" @size-change="handleSizeChangeInfo" @current-change="handleCurrentChangeInfo" :hide-on-single-page="totalInfo<10? true:false" />
                    </div>
                </div>
            </el-dialog>

        </div>
    </div>
</template>
<script>
import {
    paymentSettingList,
    paymentSettingStudentList,
    paymentSettingView,
    hashApi,
} from '@/api/index';

export default {
    data() {
        return {
            total: 0,
            pageNum: 1,
            pageSize: 15,
            totalInfo: 0,
            pageNumInfo: 1,
            pageSizeInfo: 10,
            isLoading: false,
            tableHeight: window.innerHeight - 180,
            formData: {
                fee_type: '',
                fee_total: '',
            },
            formInfo: {},
            method_auth: {},
            editId: '',
            title: '',
            search: {
                fee_type: '',
            },
            dialogVisible: false,
            dialogVisibleCheck: false,
            tableHead: [
                { title: 'ID', prop: 'id', minWidth: '100', },
                { title: '缴费类型', prop: 'fee_type_name', minWidth: '120', },
                { title: '应缴金额', prop: 'fee_total', minWidth: '100', },
                { title: '缴费班级', prop: 'class_name', minWidth: '100', },
                { title: '预计缴费人数', prop: 'predict_count', minWidth: '120', },
                { title: '预计费用', prop: 'predict_fee', minWidth: '150', },
                { title: '已交人数', prop: 'pay_count', minWidth: '150', },
                { title: '已交费用', prop: 'pay_fee', minWidth: '150', },
                { title: '未交费用', prop: 'unpay_fee', minWidth: '150', },
                { title: '创建日期', prop: 'create_time', minWidth: '150', },
            ],
            tableHeads: [
                { title: 'ID', prop: 'id', minWidth: '100', },
                { title: '学生姓名', prop: 'student_name', minWidth: '120', },
                { title: '应缴金额', prop: 'payable_fee', minWidth: '100', },
                { title: '缴费状态', prop: 'pay_status_name', minWidth: '120', },
                { title: '已缴费用', prop: 'pay_fee', minWidth: '150', },
            ],
            tableData: [],
            tableDataInfo: [],
            hash: '',
            fee_type_list: [],
        };
    },
    mounted() {
        this.getList();
        this.resView();
        this.getHash();
    },
    methods: {
        serchList() {
            this.pageNum = 1;
            this.pageSize = 15;
            this.getList()
        },
        // 监听 pagesize 改变
        handleSizeChange(newSize) {
            this.pageNum = 1;
            this.pageSize = newSize;
            this.getList();
        },
        // 监听 pagenum 改变
        handleCurrentChange(newPage) {
            this.pageNum = newPage;
            this.getList();
        },

        // 监听 pagesize 改变
        handleSizeChangeInfo(newSize) {
            this.pageNumInfo = 1;
            this.pageSizeInfo = newSize;
            this.studentList();
        },
        // 监听 pagenum 改变
        handleCurrentChangeInfo(newPage) {
            this.pageNumInfo = newPage;
            this.studentList();
        },
        async studentList() {
            let data = {
                curr: this.pageNumInfo,
                pageSize: this.pageSizeInfo,
                id: localStorage.getItem('StudentList_id')
            };
            const res = await this.axios.post(paymentSettingStudentList, data);
            if (res.data.code == 1) {
                this.tableDataInfo = res.data.data.data;
                this.totalInfo = res.data.data.total;
                this.formInfo = res.data.data.basic;
            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'error'
                });
            }
        },
        async resView() {
            const res = await this.axios.post(paymentSettingView);
            if (res.data.code == 1) {
                this.fee_type_list = res.data.data.fee_type_list;
            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'error'
                });
            }
        },
        async getList() {
            let data = {
                curr: this.pageNum,
                pageSize: this.pageSize,
                fee_type: this.search.fee_type,
            };
            const res = await this.axios.post(paymentSettingList, data);
            if (res.data.code == 1) {
                this.tableData = res.data.data.data;
                this.total = res.data.data.total;
            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'error'
                });
            }
        },
        check(row) {
            localStorage.setItem('StudentList_id', row.id)
            this.dialogVisibleCheck = true;
            this.$nextTick(() => {
                this.studentList();
            })
        },
        //获取Hash
        async getHash() {
            const res = await this.axios.post(hashApi, { page_hash: 'paymentSetting' });
            if (res.data.code == 1) {
                this.hash = res.data.data;
            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'error',
                });
            }
        },
        //学生档案详情
        async getInfo(id) {
            const res = await this.axios.post(payManagementInfo, { id: id });
            if (res.data.code == 1) {
                res.data.data.fee_type = String(res.data.data.fee_type);
                this.formData = res.data.data;
            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'error'
                });
            }
        },
        resetForm() {
            this.formData = {
                fee_type: '',
                fee_total: '',
            };
            this.$nextTick(() => {
                this.$refs['formData'].resetFields();
            })
        },
    }
};
</script>

<style >
.paymentSetting .el-table__row--level-1 {
    background: #fdfafa !important ;
}
</style>
<style lang="scss" scoped>
.paymentSetting {
    .searchA {
        display: flex;
        padding: 12px 25px;
        background: #fff;
        .el-select {
            margin-right: 24px;
        }
        .el-input {
            width: auto;
            margin-right: 24px;
        }
        .add {
            flex: 1;
            text-align: right;
        }
        .searchBtn {
            border: solid 1px #2f39ff;
            color: #2f39ff;
        }
    }
    .btn {
        width: 250px;
    }
    /deep/ .el-dialog {
        margin-top: 5vh !important;
    }
}
</style>



